import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';

import { FormControl, FormGroup } from '@angular/forms';
import { FilterService } from 'src/services/filter.service';

import { FILTER } from '../../mocks/filter.data';
import { Filter } from '../../interfaces/filter.model';
import { SORTING } from '../../mocks/sorting.data';
import { Sorting } from '../../interfaces/sorting.model';
import { SIDENAV_OUTPUT } from '../../mocks/sidenav-output.data';
import { SidenavOutput } from '../../interfaces/side-nav.model';

import { TuiDialogContext, TuiDialogService } from '@taiga-ui/core';
import { PolymorpheusTemplate } from '@tinkoff/ng-polymorpheus';
import { BreakpointObserver, BreakpointState } from '@angular/cdk/layout';
import { Subject, Subscribable, Subscription } from 'rxjs';
import { TUI_ARROW } from '@taiga-ui/kit';

@Component({
    selector: 'app-side-nav',
    templateUrl: './side-nav.component.html',
    styleUrls: ['./side-nav.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class SideNavComponent implements OnInit {

    readonly arrow = TUI_ARROW;
    readonly dialogFilterProfile: Object = { tags: [], name: '' };

    public showFilter: boolean = true;
    public showSorting: boolean = true;
    public showAddFilter: boolean = true;
    public showSidebar: boolean = false;
    public addSidebar: boolean = false;
    public size: string = 'm';

    public filterMass: Filter[] = FILTER;
    public sortMass: Sorting[] = SORTING;





    public settingsForm: FormGroup = new FormGroup({
        filters: new FormControl([]),
        sorting: new FormControl(this.sortMass[0].data),
        wholeFilters: new FormControl([FILTER]),
    });

    public dialogFilter: FormGroup = new FormGroup({
        tags: new FormControl([]),
        name: new FormControl()
    });

    private tagsMass: string[] = [];

    private laptopQuerySubs: Subscription;
    private mobileQuerySubs: Subscription;
    private filtersSubs: Subscription;
    private sortingSubs: Subscription;
    private tagsSubs: Subscription;
    private dialogFilterSubs: Subscription;





    constructor(
        private readonly filterService: FilterService,
        private changeDetector: ChangeDetectorRef,
        @Inject(TuiDialogService) private readonly dialogService: TuiDialogService,
        public breakpointObserver: BreakpointObserver
    ) { }

    ngOnInit() {

        this.filtersSubs = this.settingsForm.get('filters').valueChanges.subscribe(() => {
            this.tagsMass = [...new Set([].concat(...this.settingsForm.get('filters').value.map(elem => elem.tags)))];
            this.filterService.changeFilter(this.tagsMass);
        })
        this.sortingSubs = this.settingsForm.get('sorting').valueChanges.subscribe((sorting) => {
            this.filterService.changeSorting(sorting);
        });


        this.laptopQuerySubs = this.breakpointObserver.observe(['(max-width: 1450px)']).subscribe((state: BreakpointState) => {
            if (state.matches) {
                this.size = 's';
                this.changeDetector.detectChanges()
            } else {
                this.size = 'm';
                this.changeDetector.detectChanges()
            }
        });
        this.mobileQuerySubs = this.breakpointObserver.observe(['(max-width: 1050px)']).subscribe((state: BreakpointState) => {
            if (state.matches) {
                this.addSidebar = true;
                this.changeDetector.detectChanges()
            } else {
                this.addSidebar = false;
                this.changeDetector.detectChanges()
            }
        });


        this.dialogFilterSubs = this.dialogFilter.valueChanges.subscribe((data) => {
            if (data.tags.length !== 0 && data.name !== '') {
                this.showAddFilter = false;
            } else {
                this.showAddFilter = true;
            }
        })
        this.tagsSubs = this.filterService.tags$.subscribe((val) => {
            const filters = this.filterMass.filter((elem) => this.isContain(elem.tags, val))
            this.settingsForm.patchValue({ filters: filters }, { emitEvent: false });
        });
    }

    ngOnDestroy(): void {
        this.laptopQuerySubs.unsubscribe();
        this.mobileQuerySubs.unsubscribe();
        this.filtersSubs.unsubscribe();
        this.sortingSubs.unsubscribe();
        this.tagsSubs.unsubscribe();
        this.dialogFilterSubs.unsubscribe();
    }

    clearFilters() {
        this.settingsForm.value.filters = [];
    }

    isEqual(a, b): boolean {
        if (a.length === b.length &&
            a.sort().every((value, index) => {
                return value === b.sort()[index];
            })) {
            return true;
        }
        return false;
    }

    isContain(target: string[], container: string[]) {
        return target.every((elem) => container.includes(elem));
    }

    showDialog(content: PolymorpheusTemplate<TuiDialogContext>) {
        this.dialogService.open(content).subscribe({
            next: () => {
                let filter: Filter = {
                    name: this.dialogFilter.get('name').value,
                    tags: this.dialogFilter.get('tags').value,
                    icon: 'tuiIconIndentLarge'
                }
                this.dialogFilter.reset(this.dialogFilterProfile);
                this.filterMass = this.filterMass.concat(filter)
            }
        });
    }

    toggle(open: boolean) {
        this.showSidebar = open;
    }
}
